* {
  margin: 0;
  padding: 0;
  outline: none;
}

:root {
  --black: #050505;
  --grey: #adb5bd;
  --steel-blue: #53a5e9;
  --cream: #f5f5f5;
  /* CSS HEX */
  /* from coolors.co */
  --flickr-pink: #f72585ff;
  --pink: rgb(255, 102, 138);
  --byzantine: #b5179eff;
  --purple: #7209b7ff;
  --trypan-blue: #480ca8ff;
  --trypan-blue-2: #3a0ca3ff;
  --persian-blue: #3f37c9ff;
  --ultra-blue: #4361eeff;
  --blue: #4895efff;
  --vivid-blue: #4cc9f0ff;
}

::-webkit-scrollbar,
::-webkit-scrollbar-button,
::-webkit-scrollbar-track,
::-webkit-scrollbar-track-piece,
::-webkit-scrollbar-thumb,
::-webkit-scrollbar-corner,
::-webkit-resizer {
  display: none;
}

h1,
h2,
p {
  font-family: 'Mulish', sans-serif;
}

a {
  font-family: 'Staatliches', cursive;
  text-decoration: none;
  transition: all 0.3s ease 0s;
  color: var(--pink);
}

a:hover {
  color: var(--flickr-pink);
}

a:active {
  color: var(--byzantine);
}

button {
  border-style: none;
  background: transparent;
}

hr {
  display: block;
  height: 2px;
  border: 0;
  border-top: 2px solid var(--byzantine);
  padding: 0;
}

ul {
  list-style-type: none;
}

span {
  width: auto;
  height: auto;
}
